@media only screen and (min-width: 960px) {
  md-dialog {
    min-width: 700px;
    max-width: 700px;
  }
}
.state-loading {
  position: absolute !important;
  z-index: 1000 !important;
  width: 100% !important;
  height: 100% !important;
  background: white !important;
}
.state-loading .md-mode-indeterminate {
  top: 50%;
  margin-top: -100px !important;
}
.action-loading {
  position: absolute !important;
  z-index: 1000 !important;
  top: 20% !important;
  left: 50% !important;
  margin-top: -50px !important;
  margin-left: -30px !important;
}
.action-loading svg {
  margin-top: 10px !important;
  margin-left: 10px !important;
}
.action-loading .md-mode-indeterminate {
  width: 60px !important;
  height: 60px !important;
  background: white !important;
  -moz-border-radius: 70px !important;
  -webkit-border-radius: 70px !important;
  border-radius: 70px !important;
  -webkit-box-shadow: 0px 0px 15px -5px #000000 !important;
  -moz-box-shadow: 0px 0px 15px -5px #000000 !important;
  box-shadow: 0px 0px 15px -5px #000000 !important;
}
.anima.ng-enter {
  animation: slideInUp 0.5s both ease-in;
  z-index: 8888;
}
.anima.ng-leave {
  animation: slideOutLeft 0.5s both ease-in;
  z-index: 9999;
}
@keyframes rotateFall {
  0% {
    transform: rotateZ(0deg);
  }
  20% {
    transform: rotateZ(10deg);
    animation-timing-function: ease-out;
  }
  40% {
    transform: rotateZ(17deg);
  }
  60% {
    transform: rotateZ(16deg);
  }
  100% {
    transform: translateY(100%) rotateZ(17deg);
  }
}
/* slide in from the bottom */
@keyframes slideOutLeft {
  to {
    transform: translateX(-100%);
  }
}
/* rotate out newspaper */
@keyframes rotateOutNewspaper {
  to {
    transform: translateZ(-3000px) rotateZ(360deg);
    opacity: 0;
  }
}
/* entering animations --------------------------------------- */
/* scale up */
@keyframes scaleUp {
  from {
    opacity: 0.3;
    -webkit-transform: scale(0.8);
  }
}
/* slide in from the right */
@keyframes slideInRight {
  from {
    transform: translateX(100%);
  }
  to {
    transform: translateX(0);
  }
}
/* slide in from the bottom */
@keyframes slideInUp {
  from {
    transform: translateY(100%);
  }
  to {
    transform: translateY(0);
  }
}
